import apiClient from './apiConfig';

const TEMPLATES_URL = '/email-templates'; // L'URL de base pour l'API des templates

// Fonction pour transformer les donnes du template si ncessaire (ex: dates)
// Pour l'instant, les donnes semblent directes, mais c'est une bonne pratique de l'avoir.
const transformTemplateData = (template) => {
  if (!template) return null;
  return {
    ...template,
    // createdAt: new Date(template.createdAt), // Dcommenter si vous voulez des objets Date
    // updatedAt: new Date(template.updatedAt), // Dcommenter si vous voulez des objets Date
  };
};

const emailTemplateService = {
  /**
   * Rcuprer tous les templates d'email.
   * @returns {Promise<Array>}
   */
  async getAllTemplates() {
    try {
      const response = await apiClient.get(TEMPLATES_URL);
      // L'API retourne { success: true, templates: [...] }
      if (response.data && response.data.success && Array.isArray(response.data.templates)) {
        return response.data.templates.map(transformTemplateData);
      }
      return []; // Retourner un tableau vide en cas de format inattendu
    } catch (error) {
      console.error('Erreur lors de la rcupration de tous les templates d\'email:', error);
      throw error;
    }
  },

  /**
   * Rcuprer un template d'email par son ID.
   * @param {number} templateId - ID du template.
   * @returns {Promise<Object|null>}
   */
  async getTemplateById(templateId) {
    try {
      const response = await apiClient.get(`${TEMPLATES_URL}/${templateId}`);
      // L'API retourne { success: true, template: {...} }
      if (response.data && response.data.success && response.data.template) {
        return transformTemplateData(response.data.template);
      }
      return null; // Retourner null si le template n'est pas trouv ou format inattendu
    } catch (error) {
      console.error(`Erreur lors de la rcupration du template d\'email ${templateId}:`, error);
      // Grer spcifiquement les erreurs 404 si l'API les renvoie comme des erreurs HTTP
      if (error.response && error.response.status === 404) {
        return null;
      }
      throw error;
    }
  },

  /**
   * Mettre  jour un template d'email.
   * @param {number} templateId - ID du template  mettre  jour.
   * @param {Object} data - Donnes  mettre  jour (subject, body, description).
   * @returns {Promise<Object>}
   */
  async updateTemplate(templateId, data) {
    try {
      const response = await apiClient.put(`${TEMPLATES_URL}/${templateId}`, data);
      // L'API retourne { success: true, message: '...', template: {...} }
      if (response.data && response.data.success && response.data.template) {
        return transformTemplateData(response.data.template);
      }
      // Si la rponse n'a pas le format attendu mais est un succs, lancez une erreur spcifique
      if (response.data && response.data.success) {
          throw new Error('La mise  jour du template a russi mais la rponse API est malforme.');
      }
      // Si ce n'est pas un succs, l'intercepteur d'erreur d'apiClient devrait le grer
      // ou vous pouvez lancer une erreur ici base sur response.data.message
      throw new Error(response.data?.message || 'Erreur lors de la mise  jour du template.');
    } catch (error) {
      console.error(`Erreur lors de la mise  jour du template d\'email ${templateId}:`, error);
      throw error;
    }
  },
  
  /**
   * Crer un nouveau template d'email.
   * @param {Object} templateData - Donnes du template (name, subject, body, description).
   * @returns {Promise<Object>}
   */
  async createTemplate(templateData) {
    try {
      const response = await apiClient.post(TEMPLATES_URL, templateData);
      if (response.data && response.data.success && response.data.template) {
        return transformTemplateData(response.data.template);
      }
      throw new Error(response.data?.message || 'Erreur lors de la cration du template.');
    } catch (error) {
      console.error('Erreur lors de la cration du template d\'email:', error);
      throw error;
    }
  }
};

export default emailTemplateService; 